import React from 'react'
import SpacePlayer from '../player'
import SpaceMap from '../map'

import { connect } from 'react-redux'

import { spaceTiles } from '../../../data/maps/Space1'
import { tilesSpace } from '../../../data/maps/Space2'
import store from '../../../config/store'

function SpaceWorld(props) {
    
    if (store.getState().inside.insideWorld == 'true') {
        store.dispatch({ type: 'ADD_SPACE_TILES', payload: {
            tiles: tilesSpace,
        }})
    } else {
        store.dispatch({ type: 'ADD_SPACE_TILES', payload: {
            tiles: spaceTiles,
        }})
    }

    return (
        <div id='gameWorld'
        style={{
                position: 'relative',
                width: '800px',
                height: '560px',
                margin: '20px auto',
            }}
        >
            <SpaceMap />
            <SpacePlayer />
        </div>
        
    )
}

function mapStateToProps(state) {
    return {
        ...state.inside,   
    }
}
export default connect(mapStateToProps)(SpaceWorld)