import React from 'react'
import ReactDOM from 'react-dom'
import SpacePlayer from '../player'
import SpaceMap from '../map'
import SpaceWorld from '../world'
import {Provider} from 'react-redux'
import store from '../../../config/store'
import { SPRITE_SIZE, MAP_WIDTH, MAP_HEIGHT } from '../../../config/constants'
import walkSpriteRed from './player_red-armor.png'
import walkSprite from './player_walk.png'
import './styles.css'

const missile = require('../../../images/space/missile.png')
const missileUp = require('../../../images/space/missileup.png')
const boom = require('../../../images/space/boom.png')
const boomUp = require('../../../images/space/boomup.png')

export default function handleMovement(spacePlayer) {

    var myNextTile = []

    function getNewPosition(oldPos, direction) {
        
        switch (direction) {
            case 'WEST':
                return [ oldPos[0]-SPRITE_SIZE, oldPos[1] ]
            case 'EAST':
                return [ oldPos[0]+SPRITE_SIZE, oldPos[1] ]
            case 'NORTH':
                return [ oldPos[0], oldPos[1]-SPRITE_SIZE ]
            case 'SOUTH':
                return [ oldPos[0], oldPos[1]+SPRITE_SIZE ]
        }
    }

    function getSpriteLocation(direction, walkIndex) {
        switch(direction) {
            case 'SOUTH':
                return `${SPRITE_SIZE*walkIndex}px ${SPRITE_SIZE*0}px`
            case 'EAST':
                return `${SPRITE_SIZE*walkIndex}px ${SPRITE_SIZE*1}px`
            case 'WEST':
                return `${SPRITE_SIZE*walkIndex}px ${SPRITE_SIZE*2}px`
            case 'NORTH':
                return `${SPRITE_SIZE*walkIndex}px ${SPRITE_SIZE*3}px`
        }
    }

    function getWalkIndex() {
        const walkIndex = store.getState().spacePlayer.walkIndex
        return walkIndex >= 5 ? 0 : walkIndex + 1
    }

    function observeBoundaries(oldPos, newPos) {
        return (newPos[0] >= 0 && newPos[0] <= MAP_WIDTH - SPRITE_SIZE) &&
               (newPos[1] >= 0 && newPos[1] <= MAP_HEIGHT - SPRITE_SIZE)
    }

    function observeImpassable(oldPos, newPos) {
        const tiles = store.getState().spaceMap.tiles

        const y = newPos[1] / SPRITE_SIZE
        const x = newPos[0] / SPRITE_SIZE
        
        const nextTile = tiles[y][x]
        myNextTile = nextTile

        return (nextTile != 9 && nextTile != 10) 
    }

    function dispatchMove(direction, newPos) {
        const walkIndex = getWalkIndex()
        
        if(myNextTile===39) {
            store.dispatch({
                type: 'PLAY_GAME',
                payload: {
                    game: 'Home'
                }
              })
        }

        if(myNextTile==15) {
            direction = 'SOUTH'
            store.dispatch({
                type: 'WORLD_RENDER',
                payload: {
                    insideWorld: 'true',
                    direction: direction
                }
            })
            ReactDOM.render(
            <Provider store={store}>
                <SpaceWorld><SpaceMap /><SpacePlayer /></SpaceWorld>
            </Provider> , document.getElementById('gameWorld'))
        }

        if(myNextTile==17) {
            newPos = [320,80]
            direction = 'SOUTH'
            store.dispatch({
                type: 'WORLD_RENDER',
                payload: {
                    insideWorld: 'false',
                    position: newPos,
                    direction: direction
                }
            })
            
            ReactDOM.render(
            <Provider store={store}>
                <SpaceWorld><SpaceMap /><SpacePlayer /></SpaceWorld>
            </Provider> , document.getElementById('gameWorld'))
        }

        if(myNextTile===55) {
          direction = 'EAST'
          //<p style={{margin: '-20px'}}><b>Nice shot!</b></p>
          ReactDOM.render(<div 
            id='treasure' style={{width: '100px', color: 'white', overflow: 'hidden'}}>
            <div className='image-div'>
              <a style={{margin: '7px'}} id="missile">
                <img src={missile} />
              </a>
            </div>
            <div className='image-div'>
              <a style={{margin: '7px'}} id="boom">
                <img src={boom} />
              </a>
            </div>
          </div>,window.document.getElementById('theSpacePlayer'))
          window.document.getElementById('treasure').style.display = 'initial'
          if ( store.getState().spacePlayer.blueArmor == 'true') {
              store.dispatch({
                  type: 'MOVE_SPACE_PLAYER',
                  payload: {
                      position: newPos,
                      direction: direction,
                      walkIndex,
                      spriteLocation: getSpriteLocation(direction, walkIndex),
                      backgroundImage: `url('${walkSpriteRed}')`,
                      blueArmor: 'true',
                  }
              })
          } else {
              store.dispatch({
                  type: 'MOVE_SPACE_PLAYER',
                  payload: {
                      position: newPos,
                      direction: direction,
                      walkIndex,
                      spriteLocation: getSpriteLocation(direction, walkIndex),
                      backgroundImage: `url('${walkSprite}')`,
                      blueArmor: 'false',
                  }
              })
          }
        } else if(myNextTile===53) {
          direction = 'NORTH'
          //<p style={{margin: '-20px'}}><b>Nice shot!</b></p>
          ReactDOM.render(<div 
            id='treasure' style={{width: '100px', color: 'white', overflow: 'hidden'}}>
            <div className='image-div'>
              <a style={{margin: '7px'}} id="missileup">
                <img src={missileUp} />
              </a>
            </div>
            <div className='image-div'>
              <a style={{margin: '7px'}} id="boomup">
                <img src={boomUp} />
              </a>
            </div>
          </div>,window.document.getElementById('theSpacePlayer'))
          window.document.getElementById('treasure').style.display = 'initial'
          if ( store.getState().spacePlayer.blueArmor == 'true') {
              store.dispatch({
                  type: 'MOVE_SPACE_PLAYER',
                  payload: {
                      position: newPos,
                      direction: direction,
                      walkIndex,
                      spriteLocation: getSpriteLocation(direction, walkIndex),
                      backgroundImage: `url('${walkSpriteRed}')`,
                      blueArmor: 'true',
                  }
              })
          } else {
              store.dispatch({
                  type: 'MOVE_SPACE_PLAYER',
                  payload: {
                      position: newPos,
                      direction: direction,
                      walkIndex,
                      spriteLocation: getSpriteLocation(direction, walkIndex),
                      backgroundImage: `url('${walkSprite}')`,
                      blueArmor: 'false',
                  }
              })
          }
        } else if(myNextTile==38)  {
            ReactDOM.render(<div 
                id='treasure' style={{width: '100px', color: 'white', display: 'flex'}}>
                <p style={{margin: '-55px'}}><b>What was once dormant is now..</b></p>
                </div>,window.document.getElementById('theSpacePlayer'))
                window.document.getElementById('treasure').style.display = 'initial'

            if ( store.getState().spacePlayer.blueArmor == 'true') {
                store.dispatch({
                    type: 'MOVE_SPACE_PLAYER',
                    payload: {
                        position: newPos,
                        direction: direction,
                        walkIndex,
                        spriteLocation: getSpriteLocation(direction, walkIndex),
                        backgroundImage: `url('${walkSpriteRed}')`,
                        blueArmor: 'true',
                    }
                })
            } else {
                store.dispatch({
                    type: 'MOVE_SPACE_PLAYER',
                    payload: {
                        position: newPos,
                        direction: direction,
                        walkIndex,
                        spriteLocation: getSpriteLocation(direction, walkIndex),
                        backgroundImage: `url('${walkSprite}')`,
                        blueArmor: 'false',
                    }
                })
            }
        } else if(myNextTile==27) {
            if (window.document.getElementById('treasure')) {
                window.document.getElementById('treasure').style.display = 'none'
            }
            direction = 'SOUTH'
            ReactDOM.render(<div 
                id='blue' style={{width: '100px', color: 'white', display: 'flex'}}>
                <p style={{margin: '-35px'}}><b>Fireproof Armor!!!</b></p>
                </div>,window.document.getElementById('theSpacePlayer'))
                window.document.getElementById('blue').style.display = 'initial'
            store.dispatch({
                type: 'MOVE_SPACE_PLAYER',
                payload: {
                    position: newPos,
                    direction: direction,
                    walkIndex,
                    spriteLocation: getSpriteLocation(direction, walkIndex),
                    backgroundImage: `url('${walkSpriteRed}')`,
                    blueArmor: 'true',
                }
            })
        } else if (myNextTile===36 || myNextTile===45) {
            //direction = 'SOUTH'
            ReactDOM.render(<div 
                id='treasure' style={{width: '100px', color: 'white', display: 'flex'}}>
                <p style={{margin: '-35px'}}><b>...looks habitable.</b></p>
                </div>,window.document.getElementById('theSpacePlayer'))
                window.document.getElementById('treasure').style.display = 'initial'

            if ( store.getState().spacePlayer.blueArmor == 'true') {
                store.dispatch({
                    type: 'MOVE_SPACE_PLAYER',
                    payload: {
                        position: newPos,
                        direction: direction,
                        walkIndex,
                        spriteLocation: getSpriteLocation(direction, walkIndex),
                        backgroundImage: `url('${walkSpriteRed}')`,
                        blueArmor: 'true',
                    }
                })
            } else {
                store.dispatch({
                    type: 'MOVE_SPACE_PLAYER',
                    payload: {
                        position: newPos,
                        direction: direction,
                        walkIndex,
                        spriteLocation: getSpriteLocation(direction, walkIndex),
                        backgroundImage: `url('${walkSprite}')`,
                        blueArmor: 'false',
                    }
                })
            }
          } else if (myNextTile===48 || myNextTile===50) {
            direction = 'SOUTH'
            ReactDOM.render(<div 
                id='treasure' style={{width: '100px', color: 'white', display: 'flex'}}>
                <p style={{margin: '-35px'}}><b>Mmm... a feast!</b></p>
                </div>,window.document.getElementById('theSpacePlayer'))
                window.document.getElementById('treasure').style.display = 'initial'

            if ( store.getState().spacePlayer.blueArmor == 'true') {
                store.dispatch({
                    type: 'MOVE_SPACE_PLAYER',
                    payload: {
                        position: newPos,
                        direction: direction,
                        walkIndex,
                        spriteLocation: getSpriteLocation(direction, walkIndex),
                        backgroundImage: `url('${walkSpriteRed}')`,
                        blueArmor: 'true',
                    }
                })
            } else {
                store.dispatch({
                    type: 'MOVE_SPACE_PLAYER',
                    payload: {
                        position: newPos,
                        direction: direction,
                        walkIndex,
                        spriteLocation: getSpriteLocation(direction, walkIndex),
                        backgroundImage: `url('${walkSprite}')`,
                        blueArmor: 'false',
                    }
                })
            }
          } else if (myNextTile===52) {
            direction = 'SOUTH'
            ReactDOM.render(<div 
                id='treasure' style={{width: '100px', color: 'white', display: 'flex'}}>
                <p style={{margin: '-35px'}}><b>Whoa... it's hot down here!</b></p>
                </div>,window.document.getElementById('theSpacePlayer'))
                window.document.getElementById('treasure').style.display = 'initial'

            if ( store.getState().spacePlayer.blueArmor == 'true') {
                store.dispatch({
                    type: 'MOVE_SPACE_PLAYER',
                    payload: {
                        position: newPos,
                        direction: direction,
                        walkIndex,
                        spriteLocation: getSpriteLocation(direction, walkIndex),
                        backgroundImage: `url('${walkSpriteRed}')`,
                        blueArmor: 'true',
                    }
                })
            } else {
                store.dispatch({
                    type: 'MOVE_SPACE_PLAYER',
                    payload: {
                        position: newPos,
                        direction: direction,
                        walkIndex,
                        spriteLocation: getSpriteLocation(direction, walkIndex),
                        backgroundImage: `url('${walkSprite}')`,
                        blueArmor: 'false',
                    }
                })
            }
        }
        else {
            if (window.document.getElementById('treasure')) {
                window.document.getElementById('treasure').style.display = 'none'
            }
            if (window.document.getElementById('blue')) {
                window.document.getElementById('blue').style.display = 'none'
            }
            if ( store.getState().spacePlayer.blueArmor == 'true') {
                store.dispatch({
                    type: 'MOVE_SPACE_PLAYER',
                    payload: {
                        position: newPos,
                        direction: direction,
                        walkIndex,
                        spriteLocation: getSpriteLocation(direction, walkIndex),
                        backgroundImage: `url('${walkSpriteRed}')`,
                        blueArmor: 'true',
                    }
                })
            } else {
                store.dispatch({
                    type: 'MOVE_SPACE_PLAYER',
                    payload: {
                        position: newPos,
                        direction: direction,
                        walkIndex,
                        spriteLocation: getSpriteLocation(direction, walkIndex),
                        backgroundImage: `url('${walkSprite}')`,
                        blueArmor: 'false',
                    }
                })
            }
        }
    }

    function attemptMove(direction) {
        const oldPos = store.getState().spacePlayer.position
        const newPos = getNewPosition(oldPos, direction)

        if(observeBoundaries(oldPos, newPos) && observeImpassable(oldPos, newPos))
            dispatchMove(direction, newPos)
        
    }

    function handleKeyDown(e){
        e.preventDefault()
        switch(e.keyCode) {
            case 37:
                return attemptMove('WEST')
            case 38:
                return attemptMove('NORTH')
            case 39:
                return attemptMove('EAST')
            case 40:
                return attemptMove('SOUTH')
            default:
                console.log(e.keyCode)
        }
    }

    window.addEventListener('keydown', (e) => {
      const gameScreen = store.getState().home.game
      if (gameScreen === 'Space') {
        handleKeyDown(e)
      } else {
        return
      }
    })

    return spacePlayer
}