import React from 'react'
import { connect } from 'react-redux'
import { SPRITE_SIZE } from '../../../config/constants'


const grass = require('../../../images/space/grass.png')
const exit = require('../../../images/space/exit.png')
const purplePlanet1 = require('../../../images/space/purpleplanet1.png')
const purplePlanet2 = require('../../../images/space/purpleplanet2.png')
const purplePlanet3 = require('../../../images/space/purpleplanet3.png')
const purplePlanet4 = require('../../../images/space/purpleplanet4.png')
const blackHoleUpperLeft = require('../../../images/space/blackholeupperleft.png')
const blackHoleUpperMiddle = require('../../../images/space/blackholeuppermiddle.png')
const blackHoleUpperRight = require('../../../images/space/blackholeupperright.png')
const blackHoleLowerLeft = require('../../../images/space/blackholelowerleft.png')
const blackHoleLowerMiddle = require('../../../images/space/blackholelowermiddle.png')
const blackHoleLowerRight = require('../../../images/space/blackholelowerright.png')
const blackHoleInsideDoor = require('../../../images/space/blackholeinsidedoor.png')
const target = require('../../../images/space/target.png')
const targetDown = require('../../../images/space/targetdown.png')
const upRedArrow = require('../../../images/space/UpRedArrow.png')
const rightRedArrow = require('../../../images/space/Right_Red_Arrow.png')
const deathStar1 = require('../../../images/space/deathstar1.png')
const deathStar2 = require('../../../images/space/deathstar2.png')
const deathStar3 = require('../../../images/space/deathstar3.png')
const deathStar4 = require('../../../images/space/deathstar4.png')
const saturn1 = require('../../../images/space/saturn1.png')
const saturn2 = require('../../../images/space/saturn2.png')
const saturn3 = require('../../../images/space/saturn3.png')
const saturn4 = require('../../../images/space/saturn4.png')
const saturn5 = require('../../../images/space/saturn5.png')
const saturn6 = require('../../../images/space/saturn6.png')
const lavaPlanet1 = require('../../../images/space/lavaplanet1.png')
const lavaPlanet2 = require('../../../images/space/lavaplanet2.png')
const lavaPlanet3 = require('../../../images/space/lavaplanet3.png')
const lavaPlanet4 = require('../../../images/space/lavaplanet4.png')
const colorPlanet = require('../../../images/space/colorplanet.png')
const moonPlanet1 = require('../../../images/space/moonplanet1.png')
const moonPlanet2 = require('../../../images/space/moonplanet2.png')
const moonPlanet3 = require('../../../images/space/moonplanet3.png')
const moonPlanet4 = require('../../../images/space/moonplanet4.png')

function getTileSprite(type) {
    switch(type) {
        case 0: 
            return grass
        case 11:
            return blackHoleUpperLeft
        case 12: 
            return blackHoleUpperMiddle
        case 13:
            return blackHoleUpperRight
        case 14:
            return blackHoleLowerLeft
        case 15:
            return blackHoleLowerMiddle
        case 16:
            return blackHoleLowerRight
        case 17:
            return blackHoleInsideDoor
        case 18: 
            return purplePlanet1
        case 19:
            return purplePlanet2
        case 20:
            return purplePlanet3
        case 21:
            return purplePlanet4
        case 22:
            return saturn1
        case 23:
            return saturn2
        case 24:
            return saturn3
        case 25:
            return saturn4
        case 26:
            return saturn5
        case 27:
            return saturn6
        case 28:
            return lavaPlanet1
        case 29:
            return lavaPlanet2
        case 30:
            return lavaPlanet3
        case 31:
            return lavaPlanet4
        case 32:
            return colorPlanet
        case 33:
            return moonPlanet1
        case 34:
            return moonPlanet2
        case 35:
            return moonPlanet3
        case 36: 
            return moonPlanet4
        case 39:
            return exit
        case 53: 
            return upRedArrow
        case 54:
            return target
        case 55:
            return rightRedArrow
        case 56:
            return targetDown
        case 57:
            return deathStar1
        case 58:
            return deathStar2
        case 59:
            return deathStar3
        case 60: 
            return deathStar4
    }
}

function MapTile(props) {
    return <div 
    style={{
        display: 'inline-flex',
        backgroundColor: 'black',
        height: SPRITE_SIZE,
        width: SPRITE_SIZE,
        //backgroundImage: `url('${getTileSprite(props.tile)}')`,
    }}>
    <img src={getTileSprite(props.tile)}/>
    </div>
}

function MapRow(props) {
    return <div 
        className="row"
        style={{
            height: SPRITE_SIZE,
        }}
        >
        {
            props.tiles.map( tile => <MapTile tile={tile} />)
        }
        </div>
}

function SpaceMap(props) {
    return (
        <div
            style={{
                position: 'relative',
                top: '0px',
                left: '0px',
                width: '800px',
                height: '560px',
                border: '4px solid white',
                border: '4px solid white',
                webkitBoxShadow: '0px 0px 0px 2px blue',
                mozBoxShadow: '0px 0px 0px 2px blue',
                boxShadow: '0px 0px 0px 2px blue',
                
            }}
        >
            {
                
                props.tiles.map( row => <MapRow tiles={row} />)
                
            }
        </div>
    )
}

function mapStateToProps(state) {
    const stateSpaceTiles = state.spaceMap.tiles
    return {
        tiles: stateSpaceTiles,
        space: state.spaceMap.space,
        ...state.spaceMap
    }
}

export default connect(mapStateToProps)(SpaceMap)