import React, { Component } from 'react'
import { connect } from 'react-redux'
import Games from '../src/components/games-home'
import CastleWorld from '../src/components/castle/world'
import VolcanoWorld from '../src/components/volcano/world'
import SpaceWorld from '../src/components/space/world'
import CardGame from '../src/components/CardGame'

class App extends Component {
  render() {
    let app

    function setApp(display) {
      switch(display) {
        case 'Home': 
          return app = <Games />
        case 'Castle':
          return app = <CastleWorld />
        case 'Volcano':
          return app = <VolcanoWorld />
        case 'Space':
          return app = <SpaceWorld />
        case 'Memory':
          return app = <CardGame />
        default: return app = <Games />
      }
    }

    return (
      <div className="home">
        <div className='home-element'>
          <h1>&nbsp;&nbsp;{this.props.home.game}</h1>
        </div>
        <div className='home-element'>
          {setApp(this.props.home.game)}
        </div>
      </div>
    )
  }
}

function mapStateToProps(state) {
  return {
    home: state.home
  }
}

export default connect(mapStateToProps)(App)
